@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,200;6..12,300;6..12,400;6..12,500;6..12,600;6..12,700;6..12,800;6..12,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@700&display=swap");
html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1.2;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  margin: 0;
  padding: 0;
}

/*
//Colors
$white-block: white;
$grey-block: rgba(247, 247, 247, 1);
*/
body {
  font-family: "Nunito Sans", sans-serif;
}

img {
  width: 100%;
}

.container {
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn-green {
  height: 44px;
  padding: 0 24px;
  border-radius: 4px;
  background: rgb(26, 62, 62);
  font-size: 16px;
  font-weight: 600;
  color: rgb(253, 253, 253);
  cursor: pointer;
}

.btn-white {
  height: 52px;
  padding: 0 36px;
  border-radius: 4px;
  background: rgb(253, 253, 253);
  font-size: 16px;
  font-weight: 700;
  color: rgb(26, 62, 62);
  cursor: pointer;
}

.btn-white__mobile {
  display: none;
}
@media screen and (max-width: 999px) {
  .btn-white__mobile {
    display: block;
    height: 52px;
    padding: 0 36px;
    border-radius: 4px;
    background: rgb(253, 253, 253);
    font-size: 16px;
    font-weight: 700;
    color: rgb(26, 62, 62);
    cursor: pointer;
  }
}

.block-padding {
  padding-top: 80px;
  padding-bottom: 100px;
}

.header {
  background-image: url("/img/img/header_bg.png");
  background-repeat: no-repeat;
  padding-top: 20px;
  padding-bottom: 80px;
}

.header-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media screen and (max-width: 999px) {
  .header-nav__logo {
    width: 84px;
    height: 44px;
  }
}

.header-nav__menu {
  display: flex;
  gap: 24px;
}
@media screen and (max-width: 999px) {
  .header-nav__menu {
    display: none;
  }
}
.header-nav__menu-link {
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
  color: rgb(253, 253, 253);
}
.header-nav__menu-link:hover {
  text-decoration: underline;
  color: rgb(242, 190, 34);
}

@media screen and (max-width: 999px) {
  .header-nav__btn-green {
    display: none;
  }
}

.header-content {
  font-family: "Titillium Web", sans-serif;
  font-size: 60px;
  text-transform: uppercase;
  padding-top: 463px;
  padding-bottom: 41px;
  color: rgb(253, 253, 253);
}
@media screen and (max-width: 999px) {
  .header-content {
    font-size: 38px;
    padding-top: 234px;
    padding-bottom: 41px;
  }
}

.header-form {
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.2);
}
@media screen and (max-width: 999px) {
  .header-form {
    display: none;
  }
}

.header-form__wrapper {
  display: flex;
  justify-content: space-around;
  padding-top: 40px;
  padding-bottom: 40px;
}

.header-form__group-select {
  width: 270px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 14px 16px 14px 16px;
  margin-bottom: 8px;
  cursor: pointer;
}

.header-form__group-select-text {
  font-size: 16px;
  font-weight: 700;
  color: rgb(253, 253, 253);
}

.header-form__select-text--option {
  color: black;
  background: gray;
}

.header-form__group-text {
  font-size: 14px;
  font-weight: 400;
  color: rgb(253, 253, 253);
}

input::placeholder {
  color: rgb(253, 253, 253);
}

.no_text_into_input {
  opacity: 0;
}

.checkbox {
  position: absolute;
  height: 32px;
  width: 32px;
  top: 80px;
  right: 15px;
  z-index: 5;
  opacity: 0;
  cursor: pointer;
}
.checkbox:checked ~ .header-nav__menu-mobile {
  transform: translateX(0%);
}
.checkbox:checked + label .header-nav__btn-line:nth-child(1) {
  transform: translateY(0) rotate(45deg);
}
.checkbox:checked + label .header-nav__btn-line:nth-child(2) {
  transform: translateY(0) rotate(-45deg);
}
.checkbox:checked + label .header-nav__btn-line:nth-child(3) {
  opacity: 0;
}

.header-nav__btn {
  display: none;
  position: relative;
  width: 18px;
  height: 16px;
  border: none;
  cursor: pointer;
}
@media screen and (max-width: 999px) {
  .header-nav__btn {
    display: block;
  }
}

.header-nav__btn-line {
  position: absolute;
  width: 18px;
  height: 4px;
  left: 0px;
  border-radius: 4px;
  background-color: white;
  transition: 0.3s ease all;
}

.header-nav__btn-line-short {
  width: 10px;
  left: 8px;
}

.header-nav__btn-line:nth-child(1) {
  transform: translateY(-10px);
}

.header-nav__btn-line:nth-child(3) {
  transform: translateY(10px);
}

.header-nav__menu-mobile {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  background-color: rgb(26, 62, 62);
  transform: translateX(-100%);
  transition: 0.5s ease all;
  padding: 50px 20px 20px 20px;
}
@media screen and (max-width: 999px) {
  .header-nav__menu-mobile {
    display: block;
  }
}
.header-nav__menu-mobile--link {
  display: block;
  font-size: 14px;
  font-weight: 400;
  text-decoration: none;
  margin-bottom: 16px;
  color: white;
}
.header-nav__menu-mobile--link:hover {
  text-decoration: underline;
  color: rgb(242, 190, 34);
}

.header-nav__btn {
  transform: translateX(0);
}

.approach {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 120px;
}

.approach-left {
  position: relative;
}

.approach-left__img {
  width: 350px;
  height: 400px;
  border-radius: 24px;
}

.approach-left__img-fg {
  position: absolute;
  top: 200px;
  left: 80px;
}

.approach-right__subtitle {
  font-size: 24px;
  font-weight: 400;
  margin-bottom: 8px;
  color: rgb(242, 190, 34);
}

.approach-right__title {
  font-size: 32px;
  font-weight: 800;
  margin-bottom: 40px;
  color: rgb(24, 18, 30);
}

.approach-right__paragraph {
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 40px;
  color: rgb(24, 18, 30);
}

